.Bubble {
  position: fixed;
  right: 0;
  bottom: 0;
  max-width: 16rem;
  margin: 0.8rem;
  background: rgba(255, 255, 255, 0.9);
  padding: 0.8rem;
  border-radius: 0.1rem;
  border: 0.05rem solid #ee7f2d;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: stretch;
  font-size: 0.8rem;
  z-index: 10000; }
  .Bubble .Bubble-top {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 0.8rem; }
    .Bubble .Bubble-top .Bubble-title {
      color: #000;
      font-weight: bolder; }
    .Bubble .Bubble-top .Bubble-button {
      color: #777;
      cursor: pointer;
      text-decoration: none;
      align-self: flex-start; }
      .Bubble .Bubble-top .Bubble-button:hover {
        cursor: pointer;
        color: #ee7f2d; }
  .Bubble .Bubble-content {
    color: #777;
    font-size: 0.7rem; }
    .Bubble .Bubble-content .Bubble-link {
      color: #ee7f2d;
      text-decoration: none; }
      .Bubble .Bubble-content .Bubble-link:hover {
        font-weight: bolder;
        cursor: pointer; }
