  flex: 0 0 auto;
  width: 25vw;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: stretch; }
  .MenuPane .MenuPane-content {
    padding: 0 0 0;
    flex: 1 1 auto;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: stretch; }
    .MenuPane .MenuPane-content .MenuPane-input {
      margin: 0 0 0;
      border: none;
      border-bottom: 0.05rem solid #ddd;
      line-height: 2rem;
      padding: 0 0.8rem;
      flex: 0 0 auto; }
    .MenuPane .MenuPane-content .MenuPane-caption {
      padding: 0.8rem;
      text-align: right;
      font-size: 0.7rem; }
    .MenuPane .MenuPane-content .MenuPane-results {
      padding: 0 0 0.8rem;
      flex: 1 1 auto;
      overflow-y: auto; }
