.Demo {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  z-index: 2000; }
  .Demo .Demo-popup {
    box-sizing: border-box;
    flex: 0 0 auto;
    width: 80%;
    height: 80%;
    margin: 2rem;
    padding: 2rem;
    background-color: #fff;
    border-radius: 0.1rem;
    border: 0.05rem solid #ee7f2d;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: stretch;
    overflow: hidden; }
    .Demo .Demo-popup .Demo-top {
      flex: 1 1 auto;
      overflow: hidden;
      background-size: contain;
      background-position: center center;
      background-repeat: no-repeat; }
      .Demo .Demo-popup .Demo-top .Demo-img {
        height: auto;
        width: auto;
        max-height: 100%;
        max-width: 100%; }
    .Demo .Demo-popup .Demo-text {
      flex: 0 0 auto;
      margin-top: 0.8rem;
      border-top: 0.05rem solid #ee7f2d;
      padding: 0.8rem 0;
      color: #000; }
    .Demo .Demo-popup .Demo-buttons {
      flex: 0 0 auto;
      display: flex;
      flex-flow: row wrap;
      justify-content: flex-end;
      align-items: stretch; }
      .Demo .Demo-popup .Demo-buttons .Demo-page {
        line-height: 2rem;
        padding: 0 0.8rem; }
      .Demo .Demo-popup .Demo-buttons .Demo-button {
        padding: 0 0.8rem;
        color: #ee7f2d;
        cursor: pointer;
        font-size: 0.8rem;
        height: 2rem; }
        .Demo .Demo-popup .Demo-buttons .Demo-button:hover {
          background-color: #ee7f2d;
          color: #fff; }
        .Demo .Demo-popup .Demo-buttons .Demo-button.Demo-button-disabled {
          color: #ddd;
          cursor: default;
          background-color: inherit; }
