@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.InstructorResult {
  border: 0.05rem solid #fff; }
  .InstructorResult .InstructorResult-header {
    padding: 0.26667rem 0.4rem; }
    .InstructorResult .InstructorResult-header:hover {
      cursor: pointer; }
    .InstructorResult .InstructorResult-header .InstructorResult-top,
    .InstructorResult .InstructorResult-header .InstructorResult-middle {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between; }
    .InstructorResult .InstructorResult-header .InstructorResult-top {
      align-items: flex-start;
      font-size: 0.5em; }
      .InstructorResult .InstructorResult-header .InstructorResult-top .InstructorResult-name {
        flex: 1 1 auto;
        font-size: 1.4em;
        margin-right: 0.26667rem;
        color: #000; }
      .InstructorResult .InstructorResult-header .InstructorResult-top .InstructorResult-spinner {
        flex: 0 0 auto;
        font-size: 0.7rem;
        margin-right: 0.26667rem;
        color: #2196f3;
        animation: 0.6s spin linear infinite; }
    .InstructorResult .InstructorResult-header .InstructorResult-middle {
      align-items: flex-end;
      font-size: 0.5em; }
      .InstructorResult .InstructorResult-header .InstructorResult-middle .InstructorResult-position {
        margin-right: 0.3rem; }
    .InstructorResult .InstructorResult-header .InstructorResult-bottom {
      margin: 0.26667rem;
      font-size: 0.6em;
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.5s ease;
      cursor: text; }
      .InstructorResult .InstructorResult-header .InstructorResult-bottom .InstructorResult-detail {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis; }
  .InstructorResult .InstructorResult-courses {
    max-height: 0;
    overflow: auto;
    transition: max-height 0.5s ease; }
    .InstructorResult .InstructorResult-courses .InstructorResult-course {
      overflow: hidden;
      box-sizing: border-box; }
    .InstructorResult .InstructorResult-courses.InstructorResult-expanded {
      margin-bottom: 0.8rem;
      max-height: 70vh;
      /*
      .InstructorResult-course {
        max-height: 2.71rem;
      }*/ }
  .InstructorResult:hover {
    border-color: #ee7f2d; }
  .InstructorResult.InstructorResult-expanded {
    border-color: #ee7f2d; }
    .InstructorResult.InstructorResult-expanded .InstructorResult-header {
      border-bottom: 0.05rem solid #ddd; }
      .InstructorResult.InstructorResult-expanded .InstructorResult-header .InstructorResult-bottom {
        max-height: 4em; }
    .InstructorResult.InstructorResult-expanded .InstructorResult-courses {
      max-height: 40vh; }
